.form__input {
  width: 100%;
  border: none;
  border-bottom: 2px #ccc solid;
  font-weight: 500;
  line-height: 17px;
  font-size: 14px;
  background-color: rgba(0, 0, 0, 0%);
  outline: none;
  color: #fff;
  margin-bottom: 30px;
  height: 27px;
}

.form__input::placeholder {
  font-size: 14px;
  line-height: 17px;
  font-weight: 400;
  color: #ccc;
}