  width: 150px;
  height: 50px;
  background-color: rgba(0,0,0,0);
  border: 2px solid #fff;
  border-radius: 2px;
  box-sizing: border-box;
  background-image: url(/react-mesto-auth/static/media/icon-add.bdfb8ebb.svg);
  background-repeat: no-repeat;
  background-position: center;
