  width: 120px;
  height: 120px;
  position: absolute;
  opacity: 0;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-color: rgba(0,0,0,0%);
  border-radius: 50%;
