  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 65vh;
}

.main.main_form {
  flex-direction: row;
  min-height: 74vh;
}

.cards {
  margin: 50px auto 0;
  display: grid;
  grid-template-columns: repeat(3, 282px);
  grid-column-gap: 17px;
  -webkit-column-gap: 17px;
          column-gap: 17px;
  grid-row-gap: 20px;
  row-gap: 20px;
}

@media screen and (max-width: 900px) {
  .cards {
    grid-template-columns: 282px;
    grid-column-gap: 0;
    -webkit-column-gap: 0;
            column-gap: 0;
    width: 282px;
  }
}
.card {
  position: relative;
  background-color: #fff;
  border-radius: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.card__image {
  width: 282px;
  height: 282px;
  border-radius: 10px 10px 0 0;
  object-fit: cover;
  object-position: center;
  cursor: pointer;
}
.card__title {
  padding: 0;
  margin: 24px 0 27px 21px;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 195px;
}
.card__like {
  display: flex;
  flex-direction: column;
  width: 22px;
  margin: 22px 20px 3px 0;
}
.card__like-button {
  cursor: pointer;
  width: 22px;
  height: 19px;
  background-image: url(/react-mesto-auth/static/media/icon-like.1e75d7d8.svg);
  background-repeat: no-repeat;
  background-position: center;
  border: 0;
  background-color: rgba(0,0,0,0);
  outline: none;
  padding: 0;
  margin: 0;
}

.card__like-button:hover {
  opacity: 0.5;
}
