  background-image: url(/react-mesto-auth/static/media/icon-like_active.933106ea.svg);
}
.card__like-counter {
  font-size: 13px;
  color: #000;
  line-height: 16px;
  text-align: center;
  margin: 3px 0 0 0;
  padding: 0;
}
.card__delete {
  padding: 0;
  margin: 0;
  height: 19px;
  width: 18px;
  position: absolute;
  cursor: pointer;
  background-image: url(/react-mesto-auth/static/media/icon-del.48bb334e.svg);
  background-color: rgba(0,0,0,0);
