.tooltip__title {
  display: block;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  width: 358px;
  margin: 0 36px 60px;
}

@media screen and (max-width: 429px) {
  .tooltip__title {
    max-width: 244px;
    font-size: 20px;
    line-height: 24px;
    margin: 0 18px 50px;
  }
}